/**
 * Ê®°ÂûãÁºìÂ≠òÁÆ°ÁêÜÂô®
 * ‰ΩøÁî®IndexedDBÁºìÂ≠òÊ®°ÂûãÊñá‰ª∂ÔºåÊîØÊåÅHTTP RangeËØ∑Ê±Ç
 */

class ModelCache {
  constructor() {
    this.dbName = 'ONNXModelCache';
    this.dbVersion = 1;
    this.storeName = 'models';
    this.db = null;
  }

  /**
   * ÂàùÂßãÂåñÁºìÂ≠òÊï∞ÊçÆÂ∫ì
   */
  async init() {
    return new Promise((resolve, reject) => {
      const request = indexedDB.open(this.dbName, this.dbVersion);

      request.onerror = () => reject(request.error);
      request.onsuccess = () => {
        this.db = request.result;
        resolve();
      };

      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        if (!db.objectStoreNames.contains(this.storeName)) {
          const store = db.createObjectStore(this.storeName, { keyPath: 'url' });
          store.createIndex('timestamp', 'timestamp', { unique: false });
          store.createIndex('etag', 'etag', { unique: false });
        }
      };
    });
  }

  /**
   * Ê£ÄÊü•Ê®°ÂûãÊòØÂê¶Â∑≤ÁºìÂ≠ò
   */
  async isCached(url) {
    if (!this.db) await this.init();

    return new Promise((resolve) => {
      const transaction = this.db.transaction([this.storeName], 'readonly');
      const store = transaction.objectStore(this.storeName);
      const request = store.get(url);

      request.onsuccess = () => {
        const cached = request.result;
        if (!cached) {
          resolve(null);
          return;
        }

        // Ê£ÄÊü•ÁºìÂ≠òÊòØÂê¶ËøáÊúüÔºà24Â∞èÊó∂Ôºâ
        const maxAge = 24 * 60 * 60 * 1000; // 24Â∞èÊó∂
        if (Date.now() - cached.timestamp > maxAge) {
          this.removeFromCache(url);
          resolve(null);
          return;
        }

        resolve(cached);
      };

      request.onerror = () => resolve(null);
    });
  }

  /**
   * Ëé∑ÂèñÊ®°ÂûãÊñá‰ª∂ÔºåÊîØÊåÅHTTP RangeËØ∑Ê±Ç
   */
  async getModel(url) {
    try {
      // È¶ñÂÖàÊ£ÄÊü•ÁºìÂ≠ò
      const cached = await this.isCached(url);
      if (cached) {
        console.log(`üì¶ Loading model from cache: ${url}`);
        return cached.data;
      }

      console.log(`‚¨áÔ∏è  Fetching model from network: ${url}`);

      // Ê£ÄÊü•ÊòØÂê¶ÊîØÊåÅRangeËØ∑Ê±Ç
      const headResponse = await fetch(url, { method: 'HEAD' });
      const supportsRange = headResponse.headers.get('Accept-Ranges') === 'bytes';
      const contentLength = headResponse.headers.get('Content-Length');
      const etag = headResponse.headers.get('ETag');

      let modelArray;

      if (supportsRange && contentLength && parseInt(contentLength) > 10 * 1024 * 1024) {
        // Â§ßÊñá‰ª∂‰ΩøÁî®ÂàÜÂùó‰∏ãËΩΩ
        modelArray = await this.downloadInChunks(url, contentLength);
      } else {
        // Â∞èÊñá‰ª∂Áõ¥Êé•‰∏ãËΩΩ
        const response = await fetch(url);
        if (!response.ok) {
          throw new Error(`Failed to fetch model: ${response.statusText}`);
        }
        modelArray = await response.arrayBuffer();
      }

      // ÁºìÂ≠òÊ®°Âûã
      await this.cacheModel(url, modelArray, etag);

      return modelArray;
    } catch (error) {
      console.error(`Failed to get model ${url}:`, error);
      throw error;
    }
  }

  /**
   * ÂàÜÂùó‰∏ãËΩΩÂ§ßÊñá‰ª∂
   */
  async downloadInChunks(url, contentLength) {
    const chunkSize = 1024 * 1024; // 1MB chunks
    const chunks = [];
    let downloaded = 0;

    console.log(`üì• Downloading large model in chunks (${Math.ceil(contentLength / chunkSize)} chunks)`);

    for (let start = 0; start < contentLength; start += chunkSize) {
      const end = Math.min(start + chunkSize - 1, contentLength - 1);
      const range = `bytes=${start}-${end}`;

      const response = await fetch(url, {
        headers: { Range: range }
      });

      if (!response.ok) {
        throw new Error(`Failed to download chunk ${range}: ${response.statusText}`);
      }

      const chunk = await response.arrayBuffer();
      chunks.push(chunk);
      downloaded += chunk.byteLength;

      // Êõ¥Êñ∞ËøõÂ∫¶
      const progress = (downloaded / contentLength * 100).toFixed(1);
      console.log(`‚è≥ Download progress: ${progress}% (${downloaded}/${contentLength} bytes)`);
    }

    // ÂêàÂπ∂ÊâÄÊúâÂùó
    const totalSize = chunks.reduce((sum, chunk) => sum + chunk.byteLength, 0);
    const modelArray = new Uint8Array(totalSize);
    let offset = 0;

    for (const chunk of chunks) {
      modelArray.set(new Uint8Array(chunk), offset);
      offset += chunk.byteLength;
    }

    return modelArray;
  }

  /**
   * ÁºìÂ≠òÊ®°ÂûãÊñá‰ª∂
   */
  async cacheModel(url, data, etag = null) {
    if (!this.db) await this.init();

    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction([this.storeName], 'readwrite');
      const store = transaction.objectStore(this.storeName);

      const modelRecord = {
        url,
        data,
        timestamp: Date.now(),
        etag,
        size: data.byteLength || data.length
      };

      const request = store.put(modelRecord);
      request.onsuccess = () => resolve();
      request.onerror = () => reject(request.error);
    });
  }

  /**
   * ‰ªéÁºìÂ≠ò‰∏≠ÁßªÈô§Ê®°Âûã
   */
  async removeFromCache(url) {
    if (!this.db) await this.init();

    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction([this.storeName], 'readwrite');
      const store = transaction.objectStore(this.storeName);
      const request = store.delete(url);

      request.onsuccess = () => resolve();
      request.onerror = () => reject(request.error);
    });
  }

  /**
   * Ê∏ÖÁêÜËøáÊúüÁºìÂ≠ò
   */
  async cleanup() {
    if (!this.db) await this.init();

    const maxAge = 24 * 60 * 60 * 1000; // 24Â∞èÊó∂
    const cutoffTime = Date.now() - maxAge;

    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction([this.storeName], 'readwrite');
      const store = transaction.objectStore(this.storeName);
      const index = store.index('timestamp');
      const request = index.openCursor(IDBKeyRange.upperBound(cutoffTime));

      request.onsuccess = (event) => {
        const cursor = event.target.result;
        if (cursor) {
          cursor.delete();
          cursor.continue();
        } else {
          resolve();
        }
      };

      request.onerror = () => reject(request.error);
    });
  }

  /**
   * Ëé∑ÂèñÁºìÂ≠òÁªüËÆ°‰ø°ÊÅØ
   */
  async getStats() {
    if (!this.db) await this.init();

    return new Promise((resolve) => {
      const transaction = this.db.transaction([this.storeName], 'readonly');
      const store = transaction.objectStore(this.storeName);
      const request = store.getAll();

      request.onsuccess = () => {
        const models = request.result;
        const totalSize = models.reduce((sum, model) => sum + (model.size || 0), 0);
        const stats = {
          count: models.length,
          totalSize,
          models: models.map(model => ({
            url: model.url,
            size: model.size,
            timestamp: model.timestamp,
            age: Date.now() - model.timestamp
          }))
        };
        resolve(stats);
      };

      request.onerror = () => resolve({ count: 0, totalSize: 0, models: [] });
    });
  }
}

export default ModelCache;