/**
 * ONNX Worker Proxy
 *
 * Â∞ÅË£Ö Worker ÈÄö‰ø°ÔºåÊèê‰æõ Promise API
 * ‰ΩøÁî® Proxy Ê®°ÂºèËÆ©Ë∞ÉÁî® Worker Â∞±ÂÉèË∞ÉÁî®Êú¨Âú∞ÂáΩÊï∞‰∏ÄÊ†∑ÁÆÄÂçï
 *
 * ‰ΩøÁî®Á§∫‰æã:
 * ```javascript
 * import workerUrl from 'onnx-web-framework/worker?worker&url'
 * import { createOnnxWorkerProxy } from 'onnx-web-framework'
 *
 * const worker = new Worker(workerUrl, { type: 'module' })
 * const proxy = createOnnxWorkerProxy(worker)
 *
 * await proxy.initialize({ wasmPaths: '/wasm/' })
 * await proxy.loadModel('model', modelBuffer)
 * const result = await proxy.run('model', inputs)
 * ```
 */

import {
  WorkerMessageType,
  InitializeConfig,
  LoadModelRequest,
  RunInferenceRequest,
  WorkerResponse
} from './worker-types.js';

/**
 * ONNX Worker ‰ª£ÁêÜÁ±ª
 *
 * Â∞ÅË£Ö Worker ÈÄö‰ø°ÔºåÊèê‰æõ Promise API
 */
export class ONNXWorkerProxy {
  /**
   * ÊûÑÈÄ†ÂáΩÊï∞
   *
   * @param {Worker} worker - Web Worker ÂÆû‰æã
   */
  constructor(worker) {
    /**
     * Web Worker ÂÆû‰æã
     * @type {Worker}
     * @private
     */
    this.worker = worker;

    /**
     * ËØ∑Ê±Ç ID ËÆ°Êï∞Âô®
     * @type {number}
     * @private
     */
    this.requestId = 0;

    /**
     * ÂæÖÂ§ÑÁêÜÁöÑËØ∑Ê±Ç Map
     * @type {Map<number, {resolve: Function, reject: Function, timeout: number}>}
     * @private
     */
    this.pendingRequests = new Map();

    /**
     * ÈªòËÆ§ËØ∑Ê±ÇË∂ÖÊó∂Êó∂Èó¥ÔºàÊØ´ÁßíÔºâ
     * @type {number}
     * @private
     */
    this.defaultTimeout = 60000; // 60Áßí

    /**
     * ÊòØÂê¶Â∑≤ÈáäÊîæ
     * @type {boolean}
     * @private
     */
    this.isDisposed = false;

    // ÁªëÂÆöÊ∂àÊÅØÂ§ÑÁêÜÂô®
    this.worker.onmessage = this._handleMessage.bind(this);

    // ÁªëÂÆöÈîôËØØÂ§ÑÁêÜÂô®
    this.worker.onerror = this._handleError.bind(this);
  }

  /**
   * Â§ÑÁêÜ Worker Ê∂àÊÅØ
   * @private
   *
   * @param {MessageEvent<WorkerResponse>} e - Ê∂àÊÅØ‰∫ã‰ª∂
   */
  _handleMessage(e) {
    if (this.isDisposed) return;

    const res = e.data;
    const pending = this.pendingRequests.get(res.id);

    if (!pending) {
      console.warn(`[WorkerProxy] Ê≤°ÊúâÊâæÂà∞ ID ‰∏∫ ${res.id} ÁöÑÂæÖÂ§ÑÁêÜËØ∑Ê±Ç`);
      return;
    }

    // Ê∏ÖÁêÜË∂ÖÊó∂ÂÆöÊó∂Âô®
    if (pending.timeout) {
      clearTimeout(pending.timeout);
    }

    // ‰ªéÂæÖÂ§ÑÁêÜÂàóË°®‰∏≠ÁßªÈô§
    this.pendingRequests.delete(res.id);

    // Â§ÑÁêÜÂìçÂ∫î
    if (res.type === WorkerMessageType.ERROR || res.error) {
      pending.reject(new Error(res.error || 'Unknown error'));
    } else {
      pending.resolve(res.data);
    }
  }

  /**
   * Â§ÑÁêÜ Worker ÈîôËØØ
   * @private
   *
   * @param {ErrorEvent} e - ÈîôËØØ‰∫ã‰ª∂
   */
  _handleError(e) {
    console.error('[WorkerProxy] Worker ÈîôËØØ:', e.message, e);

    // ÊãíÁªùÊâÄÊúâÂæÖÂ§ÑÁêÜÁöÑËØ∑Ê±Ç
    for (const [id, pending] of this.pendingRequests.entries()) {
      if (pending.timeout) {
        clearTimeout(pending.timeout);
      }
      pending.reject(new Error(`Worker error: ${e.message}`));
    }

    this.pendingRequests.clear();
  }

  /**
   * ÂèëÈÄÅËØ∑Ê±ÇÂà∞ Worker
   * @private
   *
   * @param {string} type - ËØ∑Ê±ÇÁ±ªÂûã
   * @param {Object} data - ËØ∑Ê±ÇÊï∞ÊçÆ
   * @param {number} timeout - Ë∂ÖÊó∂Êó∂Èó¥ÔºàÊØ´ÁßíÔºâ
   * @returns {Promise<any>}
   */
  async _request(type, data = {}, timeout = null) {
    if (this.isDisposed) {
      throw new Error('WorkerProxy has been disposed');
    }

    const id = ++this.requestId;
    const reqTimeout = timeout || this.defaultTimeout;

    return new Promise((resolve, reject) => {
      // ÂàõÂª∫Ë∂ÖÊó∂ÂÆöÊó∂Âô®
      const timeoutId = setTimeout(() => {
        this.pendingRequests.delete(id);
        reject(new Error(`Request timeout: ${type} (${reqTimeout}ms)`));
      }, reqTimeout);

      // ‰øùÂ≠òÂæÖÂ§ÑÁêÜËØ∑Ê±Ç
      this.pendingRequests.set(id, {
        resolve,
        reject,
        timeout: timeoutId
      });

      // ÂèëÈÄÅÊ∂àÊÅØÂà∞ Worker
      try {
        this.worker.postMessage({
          id,
          type,
          ...data
        });
      } catch (error) {
        // ÂèëÈÄÅÂ§±Ë¥•ÔºåÁ´ãÂç≥Ê∏ÖÁêÜ
        clearTimeout(timeoutId);
        this.pendingRequests.delete(id);
        reject(error);
      }
    });
  }

  /**
   * ÂàùÂßãÂåñ Worker
   *
   * @param {Partial<InitializeConfig>} config - ÂàùÂßãÂåñÈÖçÁΩÆ
   * @returns {Promise<void>}
   *
   * @example
   * await proxy.initialize({
   *   wasmPaths: 'https://cdn.jsdelivr.net/npm/onnxruntime-web@1.18.0/dist/',
   *   numThreads: 4
   * })
   */
  async initialize(config = {}) {
    const initConfig = new InitializeConfig(config);
    await this._request(WorkerMessageType.INITIALIZE, { config: initConfig });
    console.log('[WorkerProxy] ‚úÖ Worker ÂàùÂßãÂåñÂÆåÊàê');
  }

  /**
   * Âä†ËΩΩÊ®°Âûã
   *
   * @param {string} modelName - Ê®°ÂûãÂêçÁß∞ÔºàÁî®‰∫éÂêéÁª≠ÂºïÁî®Ôºâ
   * @param {ArrayBuffer} modelBuffer - Ê®°ÂûãÊï∞ÊçÆÔºàArrayBufferÔºâ
   * @param {Object} sessionOptions - Session ÈÖçÁΩÆÈÄâÈ°π
   * @returns {Promise<{modelName: string, inputNames: string[], outputNames: string[]}>}
   *
   * @example
   * const modelResponse = await fetch('/models/model.onnx')
   * const modelBuffer = await modelResponse.arrayBuffer()
   * const info = await proxy.loadModel('my-model', modelBuffer)
   * console.log('ËæìÂÖ•:', info.inputNames)
   * console.log('ËæìÂá∫:', info.outputNames)
   */
  async loadModel(modelName, modelBuffer, sessionOptions = {}) {
    if (!modelName) {
      throw new Error('modelName is required');
    }
    if (!modelBuffer) {
      throw new Error('modelBuffer is required');
    }

    const result = await this._request(WorkerMessageType.LOAD_MODEL, {
      modelName,
      modelBuffer,
      sessionOptions
    });

    console.log(`[WorkerProxy] ‚úÖ Ê®°Âûã '${modelName}' Âä†ËΩΩÊàêÂäü`);
    return result;
  }

  /**
   * ËøêË°åÊé®ÁêÜ
   *
   * @param {string} modelName - Ê®°ÂûãÂêçÁß∞
   * @param {Record<string, TensorData>} inputs - ËæìÂÖ•Âº†ÈáèÂ≠óÂÖ∏
   * @returns {Promise<Record<string, TensorData>>} ËæìÂá∫Âº†ÈáèÂ≠óÂÖ∏
   *
   * @example
   * const result = await proxy.run('my-model', {
   *   input: {
   *     data: new Float32Array([1, 2, 3]),
   *     dims: [1, 3],
   *     type: 'float32'
   *   }
   * })
   */
  async run(modelName, inputs) {
    if (!modelName) {
      throw new Error('modelName is required');
    }
    if (!inputs || Object.keys(inputs).length === 0) {
      throw new Error('inputs is required');
    }

    return await this._request(WorkerMessageType.RUN_INFERENCE, {
      modelName,
      inputs
    });
  }

  /**
   * ÈáäÊîæ Worker ËµÑÊ∫ê
   *
   * @returns {Promise<void>}
   *
   * @example
   * await proxy.dispose()
   */
  async dispose() {
    if (this.isDisposed) {
      console.warn('[WorkerProxy] Â∑≤ÁªèÈáäÊîæËøá‰∫Ü');
      return;
    }

    console.log('[WorkerProxy] Ê≠£Âú®ÈáäÊîæ...');

    // ÂèëÈÄÅÈáäÊîæÊ∂àÊÅØ
    try {
      await this._request(WorkerMessageType.DISPOSE, {}, 5000);
    } catch (error) {
      console.warn('[WorkerProxy] ÈáäÊîæÊ∂àÊÅØÂèëÈÄÅÂ§±Ë¥•:', error);
    }

    // Ê∏ÖÁêÜÊâÄÊúâÂæÖÂ§ÑÁêÜËØ∑Ê±Ç
    for (const [id, pending] of this.pendingRequests.entries()) {
      if (pending.timeout) {
        clearTimeout(pending.timeout);
      }
      pending.reject(new Error('Worker disposed'));
    }

    this.pendingRequests.clear();

    // ÁªàÊ≠¢ Worker
    this.worker.terminate();

    // Ê†áËÆ∞‰∏∫Â∑≤ÈáäÊîæ
    this.isDisposed = true;

    console.log('[WorkerProxy] ‚úÖ ÈáäÊîæÂÆåÊàê');
  }

  /**
   * Ê£ÄÊü• Worker ÊòØÂê¶Â∑≤ÈáäÊîæ
   *
   * @returns {boolean}
   */
  get disposed() {
    return this.isDisposed;
  }
}

/**
 * ÂàõÂª∫ ONNX Worker ‰ª£ÁêÜ
 *
 * ËøôÊòØÊé®ËçêÁöÑÂàõÂª∫ Worker Proxy ÁöÑÊñπÂºè
 *
 * @param {Worker} worker - Web Worker ÂÆû‰æã
 * @returns {ONNXWorkerProxy} Worker ‰ª£ÁêÜÂÆû‰æã
 *
 * @example
 * // Vite È°πÁõÆ
 * import workerUrl from 'onnx-web-framework/worker?worker&url'
 * import { createOnnxWorkerProxy } from 'onnx-web-framework'
 *
 * const worker = new Worker(workerUrl, { type: 'module' })
 * const proxy = createOnnxWorkerProxy(worker)
 *
 * await proxy.initialize()
 * await proxy.loadModel('model', modelBuffer)
 * const result = await proxy.run('model', inputs)
 */
export function createOnnxWorkerProxy(worker) {
  return new ONNXWorkerProxy(worker);
}

// ÈªòËÆ§ÂØºÂá∫
export default ONNXWorkerProxy;
