/**
 * ONNX Web Framework
 * Âü∫‰∫éONNX Runtime WebÊûÑÂª∫ÔºåÊîØÊåÅÊ®°ÂûãÁºìÂ≠ò
 */

import ModelCache from './model-cache.js';

// Á°Æ‰øùortÂú®ÂÖ®Â±ÄÂèØÁî®
import * as ort from 'onnxruntime-web';
if (typeof globalThis !== 'undefined') {
  globalThis.ort = ort;
}

class ONNXWebFramework {
  constructor(options = {}) {
    this.options = {
      // ÁºìÂ≠òÈÖçÁΩÆ
      enableCache: options.enableCache !== false, // ÈªòËÆ§ÂêØÁî®ÁºìÂ≠ò
      cacheMaxAge: options.cacheMaxAge || 24 * 60 * 60 * 1000, // 24Â∞èÊó∂

      // ORTÈÖçÁΩÆ
      executionProviders: options.executionProviders || ['wasm'],
      enableProfiling: options.enableProfiling || false,
      debug: options.debug || false,
      logLevel: options.logLevel || 'warning',
      numThreads: options.numThreads || 0,

      // WASMË∑ØÂæÑÈÖçÁΩÆÔºàÂèØÈÄâÔºåÈªòËÆ§ËÆ©ÊâìÂåÖÂ∑•ÂÖ∑Ëá™Âä®Â§ÑÁêÜÔºâ
      // Âè™ÊúâÂú®ÈúÄË¶ÅËá™ÂÆö‰πâË∑ØÂæÑÊó∂ÊâçËÆæÁΩÆ‰∏∫Â≠óÁ¨¶‰∏≤
      wasmPaths: options.wasmPaths || null,
      ...options
    };

    // ÁºìÂ≠òÂíåÊ®°Âûã
    this.modelCache = new ModelCache();
    this.models = new Map();
    this.isInitialized = false;
  }

  /**
   * ÂàùÂßãÂåñÊ°ÜÊû∂
   */
  async initialize() {
    try {
      console.log('üöÄ Initializing ONNX Web Framework...');

      // ‰∏çËÆæÁΩÆ wasmPathsÔºåËÆ©ÊâìÂåÖÂ∑•ÂÖ∑Ëá™Âä®Â§ÑÁêÜ
      // Â¶ÇÈúÄËá™ÂÆö‰πâË∑ØÂæÑÔºåÂèØÈÄöËøá options.wasmPaths ‰º†ÂÖ•
      if (this.options.wasmPaths && typeof this.options.wasmPaths === 'string') {
        ort.env.wasm.wasmPaths = this.options.wasmPaths;
        console.log(`üìÅ Using custom WASM paths: ${this.options.wasmPaths}`);
      } else {
        console.log('üìÅ Using default WASM loader (bundle tool will handle it)');
      }

      // ÂàùÂßãÂåñÁºìÂ≠ò
      if (this.options.enableCache) {
        await this.modelCache.init();
        await this.modelCache.cleanup();
        console.log('‚úÖ Model cache initialized');
      }

      this.isInitialized = true;
      console.log('‚úÖ ONNX Web Framework initialized successfully');

      // ÊâìÂç∞ÁºìÂ≠òÁªüËÆ°
      if (this.options.enableCache) {
        const stats = await this.modelCache.getStats();
        console.log(`üìä Cache stats: ${stats.count} models, ${(stats.totalSize / 1024 / 1024).toFixed(2)}MB`);
      }

    } catch (error) {
      console.error('‚ùå Failed to initialize framework:', error);
      throw error;
    }
  }


  /**
   * Âä†ËΩΩÊ®°Âûã
   */
  async loadModel(name, modelSource, sessionOptions = {}) {
    if (!this.isInitialized) {
      throw new Error('Framework not initialized. Call initialize() first.');
    }

    console.log(`üì¶ Loading model '${name}'...`);

    try {
      // Ëé∑ÂèñÊ®°ÂûãÊï∞ÊçÆ
      let modelArray;
      let modelFormat = 'unknown';

      if (typeof modelSource === 'string') {
        // ‰ºòÂÖàÂ∞ùËØïORTÊ†ºÂºè
        const ortSource = modelSource.replace(/\.onnx$/, '.ort');

        try {
          modelArray = await this.modelCache.getModel(ortSource);
          modelFormat = 'ort';
          console.log(`‚úÖ Using ORT format model: ${ortSource}`);
        } catch (ortError) {
          // ÂõûÈÄÄÂà∞ONNXÊ†ºÂºè
          console.log(`‚ö†Ô∏è  ORT format not available, using ONNX format`);
          modelArray = await this.modelCache.getModel(modelSource);
          modelFormat = 'onnx';
        }
      } else if (modelSource instanceof Uint8Array || modelSource instanceof ArrayBuffer) {
        modelArray = modelSource;
        modelFormat = 'array';
      } else {
        throw new Error('Invalid model source. Must be URL, Uint8Array, or ArrayBuffer.');
      }

      // Âú®‰∏ªÁ∫øÁ®ãÂä†ËΩΩÊ®°Âûã
      console.log('‚ö†Ô∏è  Loading model in main thread (UI may be blocked during inference)');
      const finalOptions = {
        executionProviders: sessionOptions.executionProviders || this.options.executionProviders,
        enableProfiling: sessionOptions.enableProfiling || this.options.enableProfiling,
        ...sessionOptions
      };

      const session = await ort.InferenceSession.create(modelArray, finalOptions);

      // ‰øùÂ≠òÊ®°Âûã‰ø°ÊÅØ
      this.models.set(name, {
        session,
        modelFormat,
        modelPath: modelSource,
        inputNames: session.inputNames,
        outputNames: session.outputNames
      });

      console.log(`‚úÖ Model '${name}' loaded successfully (${modelFormat})`);
      return {
        modelName: name,
        loaded: true,
        inputNames: session.inputNames,
        outputNames: session.outputNames
      };

    } catch (error) {
      console.error(`‚ùå Failed to load model '${name}':`, error);
      throw error;
    }
  }

  /**
   * ÊâßË°åÊé®ÁêÜ
   */
  async run(modelName, feeds) {
    if (!this.isInitialized) {
      throw new Error('Framework not initialized. Call initialize() first.');
    }

    const model = this.models.get(modelName);
    if (!model) {
      throw new Error(`Model '${modelName}' not loaded`);
    }

    try {
      // Âú®‰∏ªÁ∫øÁ®ãÊâßË°åÊé®ÁêÜ
      const result = await model.session.run(feeds);
      return result;

    } catch (error) {
      console.error(`‚ùå Failed to run inference with model '${modelName}':`, error);
      throw error;
    }
  }

  /**
   * ÊâßË°åÊé®ÁêÜÔºàÂ∏¶È¢ÑÂ§ÑÁêÜÔºâ
   */
  async predict(modelName, rawData, options = {}) {
    throw new Error('predict() not implemented. Please use run() directly with preprocessed tensors.');
  }

  /**
   * È¢ÑÂ§ÑÁêÜËæìÂÖ•Êï∞ÊçÆ
   */
  async preprocessInput(model, rawData, preprocessOptions) {
    const processed = {};

    for (const [inputName, inputMeta] of Object.entries(model.inputMetadata)) {
      let inputTensor;

      if (rawData instanceof HTMLImageElement || rawData instanceof HTMLCanvasElement) {
        // ÂõæÂÉèÈ¢ÑÂ§ÑÁêÜ
        inputTensor = await this.preprocessImage(rawData, inputMeta, preprocessOptions);
      } else if (Array.isArray(rawData) || rawData instanceof Float32Array) {
        // Êï∞ÁªÑÊï∞ÊçÆ
        inputTensor = {
          data: rawData,
          shape: preprocessOptions.resize || inputMeta.shape || [1, rawData.length]
        };
      } else if (typeof rawData === 'object' && rawData[inputName]) {
        // Â∑≤ÁªèÊòØÂ§ÑÁêÜËøáÁöÑÂØπË±°
        inputTensor = rawData[inputName];
      } else {
        throw new Error(`Unsupported input data type for '${inputName}'`);
      }

      processed[inputName] = inputTensor;
    }

    return processed;
  }

  /**
   * ÂõæÂÉèÈ¢ÑÂ§ÑÁêÜ
   */
  async preprocessImage(image, inputMeta, options) {
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');

    // Ë∞ÉÊï¥Â∞∫ÂØ∏
    const targetSize = options.resize || [224, 224];
    canvas.width = targetSize[1];
    canvas.height = targetSize[0];

    // ÁªòÂà∂ÂõæÂÉè
    ctx.drawImage(image, 0, 0, canvas.width, canvas.height);

    // Ëé∑ÂèñÂÉèÁ¥†Êï∞ÊçÆ
    const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
    const pixels = imageData.data;

    // ËΩ¨Êç¢‰∏∫Ê®°ÂûãÊâÄÈúÄÁöÑÊ†ºÂºè
    const isRGB = options.colorFormat === 'rgb';
    const normalize = options.normalization || 'zeroToOne';
    const channels = isRGB ? 3 : 4; // RGBÊàñRGBA
    const data = new Float32Array(canvas.width * canvas.height * channels);

    for (let i = 0, j = 0; i < pixels.length; i += 4, j += channels) {
      // RGBÈÄöÈÅì
      if (isRGB) {
        data[j] = pixels[i] / 255;     // R
        data[j + 1] = pixels[i + 1] / 255; // G
        data[j + 2] = pixels[i + 2] / 255; // B
      } else {
        data[j] = pixels[i] / 255;     // R
        data[j + 1] = pixels[i + 1] / 255; // G
        data[j + 2] = pixels[i + 2] / 255; // B
        data[j + 3] = pixels[i + 3] / 255; // A
      }
    }

    return {
      data,
      shape: [1, channels, canvas.height, canvas.width] // NCHWÊ†ºÂºè
    };
  }

  /**
   * Ëé∑ÂèñÊ®°Âûã‰ø°ÊÅØ
   */
  getModelInfo(modelName) {
    return this.models.get(modelName);
  }

  /**
   * ÂàóÂá∫Â∑≤Âä†ËΩΩÁöÑÊ®°Âûã
   */
  listModels() {
    return Array.from(this.models.keys());
  }

  /**
   * Âç∏ËΩΩÊ®°Âûã
   */
  async unloadModel(modelName) {
    const model = this.models.get(modelName);
    if (model && model.session) {
      await model.session.release();
    }
    this.models.delete(modelName);
    console.log(`üóëÔ∏è  Model '${modelName}' unloaded`);
  }

  /**
   * Ëé∑ÂèñÁºìÂ≠òÁªüËÆ°
   */
  async getCacheStats() {
    if (!this.options.enableCache) {
      return { enabled: false };
    }

    return await this.modelCache.getStats();
  }

  /**
   * Ê∏ÖÁêÜÁºìÂ≠ò
   */
  async clearCache() {
    if (!this.options.enableCache) {
      return;
    }

    await this.modelCache.cleanup();
    console.log('üßπ Cache cleared');
  }

  /**
   * Ê∏ÖÁêÜÊâÄÊúâËµÑÊ∫ê
   */
  async dispose() {
    // Ê∏ÖÁêÜWorker
    if (this.workerManager) {
      await this.workerManager.dispose();
      this.workerManager = null;
    }

    // Ê∏ÖÁêÜÊ®°Âûã
    this.models.clear();

    console.log('üßπ ONNX Web Framework disposed');
  }
}

export default ONNXWebFramework;